#app {
  background-color: #201f23;
  font-family: 'Roboto', sans-serif;
  color: white;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  height: 100%;
  min-height: 100vh;
  width: 100%;
}

.rant-view__title {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
  background-color: #20231f;
  color: white;
}

#status {
  opacity: 0;
}


/*** RantsList ***/

.rant-view__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 95%;

}
.rant-view__list-row {
  border: 1px solid #dcdecd;
  box-sizing: border-box;
  margin: 1% auto;
  width: 100%;
  /*height: 200px;
  */
}
.rant-view__list-content {
  padding: 2% 1%;
  box-sizing: border-box;
  width: 100%;
  display: inline-block;
  text-align: left;
  background: #393939;
}
.rant-view__content-left {
  float: left;
  width: 30%;
  box-sizing: border-box;
  padding: 1%;
}
.rant-view__content-right {
  float: right;
  width: 70%;
  box-sizing: border-box;
  padding: 1%;
}
.rant-view__amount {
  font-size: 1.8rem;
  font-weight: 700;
  color: #8eff8e;
}